import React, { useState } from 'react';

const SECTORS = [
  'Tecnología',
  'Salud',
  'Finanzas',
  'Consumo',
  'Energía',
  'Industriales',
  'Materiales',
  'Servicios',
  'Inmobiliario',
  'Utilidades',
  'Telecomunicaciones',
];

const ETFS = [
  { ticker: 'VOO', name: 'Vanguard S&P 500 ETF', description: 'Sigue el índice S&P 500, que incluye las 500 empresas más grandes de EE.UU.' },
  { ticker: 'QQQ', name: 'Invesco QQQ Trust', description: 'Sigue el índice Nasdaq-100, enfocado en empresas tecnológicas y de crecimiento.' },
  { ticker: 'SPY', name: 'SPDR S&P 500 ETF', description: 'El ETF más líquido que sigue el índice S&P 500.' },
  { ticker: 'VTI', name: 'Vanguard Total Stock Market ETF', description: 'Exposición a todo el mercado de acciones de EE.UU., incluyendo pequeña, mediana y gran capitalización.' },
  { ticker: 'IEMG', name: 'iShares Core MSCI Emerging Markets ETF', description: 'Exposición a mercados emergentes como China, India, Brasil, etc.' },
  { ticker: 'VEA', name: 'Vanguard FTSE Developed Markets ETF', description: 'Exposición a mercados desarrollados fuera de EE.UU. como Europa, Japón y Canadá.' },
];

const REGIONS = ['US', 'EU', 'ASIA', 'LATAM', 'GLOBAL'];

const PortfolioForm = ({ onSubmit, loading, portfolioType }) => {
  // Estados para todos los tipos de portfolio
  const [amount, setAmount] = useState('1000');
  const [region, setRegion] = useState('US,EU');
  const [n_stocks, setNStocks] = useState('5');
  
  // Estados específicos por tipo
  const [horizon, setHorizon] = useState('largo');
  const [risk, setRisk] = useState('medio');
  const [selectedSectors, setSelectedSectors] = useState([]);
  
  // Generar el objeto de datos adecuado según el tipo de portfolio
  const generateFormData = () => {
    const baseData = { amount: parseFloat(amount) };
    
    switch(portfolioType) {
      case 'optimize':
        return {
          ...baseData,
          horizon,
          risk,
          sectors: selectedSectors.length > 0 ? selectedSectors : undefined
        };
      
      case 'value':
        return {
          ...baseData,
          region,
          n_stocks: parseInt(n_stocks)
        };
        
      case 'growth':
        return {
          ...baseData,
          region,
          n_stocks: parseInt(n_stocks)
        };
        
      case 'disruptive':
        return {
          ...baseData,
          region,
          n_stocks: parseInt(n_stocks)
        };
        
      default:
        return baseData;
    }
  };

  const handleSectorChange = (sector) => {
    setSelectedSectors(prev =>
      prev.includes(sector)
        ? prev.filter(s => s !== sector)
        : [...prev, sector]
    );
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const formData = generateFormData();
    onSubmit(formData);
  };

  // Renderizar campos según el tipo de portfolio
  const renderFormFields = () => {
    switch(portfolioType) {
      case 'optimize':
        return (
          <>
            <div className="form-group">
              <label>Horizonte de inversión:</label>
              <div className="radio-group">
                <label>
                  <input 
                    type="radio" 
                    value="corto" 
                    checked={horizon === 'corto'} 
                    onChange={() => setHorizon('corto')} 
                  /> 
                  Corto (&lt; 5 años)
                </label>
                <label>
                  <input 
                    type="radio" 
                    value="medio" 
                    checked={horizon === 'medio'} 
                    onChange={() => setHorizon('medio')} 
                  /> 
                  Medio (5-10 años)
                </label>
                <label>
                  <input 
                    type="radio" 
                    value="largo" 
                    checked={horizon === 'largo'} 
                    onChange={() => setHorizon('largo')} 
                  /> 
                  Largo (&gt; 10 años)
                </label>
              </div>
            </div>
            
            <div className="form-group">
              <label>Perfil de riesgo:</label>
              <div className="radio-group">
                <label>
                  <input 
                    type="radio" 
                    value="bajo" 
                    checked={risk === 'bajo'} 
                    onChange={() => setRisk('bajo')} 
                  /> 
                  Conservador
                </label>
                <label>
                  <input 
                    type="radio" 
                    value="medio" 
                    checked={risk === 'medio'} 
                    onChange={() => setRisk('medio')} 
                  /> 
                  Equilibrado
                </label>
                <label>
                  <input 
                    type="radio" 
                    value="alto" 
                    checked={risk === 'alto'} 
                    onChange={() => setRisk('alto')} 
                  /> 
                  Agresivo
                </label>
              </div>
            </div>
            
            <div className="form-group">
              <label>Sectores preferidos:</label>
              <div className="checkbox-grid">
                {SECTORS.map(sector => (
                  <label key={sector}>
                    <input
                      type="checkbox"
                      checked={selectedSectors.includes(sector)}
                      onChange={() => handleSectorChange(sector)}
                    /> 
                    {sector}
                  </label>
                ))}
              </div>
            </div>
          </>
        );
        
      case 'value':
      case 'growth':
      case 'disruptive':
        return (
          <>
            <div className="form-group">
              <label>Región:</label>
              <select 
                value={region} 
                onChange={(e) => setRegion(e.target.value)}
              >
                <option value="US">Estados Unidos</option>
                <option value="EU">Europa</option>
                <option value="ASIA">Asia</option>
                <option value="LATAM">Latinoamérica</option>
                <option value="US,EU">EE.UU. y Europa</option>
                <option value="GLOBAL">Global</option>
              </select>
            </div>
            
            <div className="form-group">
              <label>Número de acciones:</label>
              <input
                type="number"
                min="3"
                max="10"
                value={n_stocks}
                onChange={(e) => setNStocks(e.target.value)}
                required
              />
            </div>
          </>
        );
        
      default:
        return null;
    }
  };

  // Devolver título según el tipo
  const getFormTitle = () => {
    switch(portfolioType) {
      case 'optimize': return 'Optimización de Portafolio';
      case 'value': return 'Portafolio Value';
      case 'growth': return 'Portafolio Growth';
      case 'disruptive': return 'Portafolio de Empresas Disruptivas';
      default: return 'Portafolio Personalizado';
    }
  };

  return (
    <form onSubmit={handleSubmit}>
      <h2>{getFormTitle()}</h2>
      
      <div className="form-group">
        <label>Monto a invertir:</label>
        <input
          type="number"
          min="500"
          step="100"
          value={amount}
          onChange={(e) => setAmount(e.target.value)}
          placeholder="$1000"
          required
        />
      </div>
      
      {renderFormFields()}
      
      <button type="submit" disabled={loading}>
        {loading ? 'Procesando...' : 'Generar Portafolio'}
      </button>
    </form>
  );
};

export default PortfolioForm;
