import React, { useState } from 'react';
import './App.css';

// Componentes
import PortfolioForm from './components/PortfolioForm';
import PortfolioDashboard from './components/PortfolioDashboard';

function App() {
  // Estados para manejar datos y UI
  const [portfolioData, setPortfolioData] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [activeTab, setActiveTab] = useState('optimize'); // optimize, value, growth, disruptive

  // Maneja el envío del formulario
  const handleFormSubmit = async (formData) => {
    setLoading(true);
    setError('');
    
    try {
      // Determinar el endpoint según el tipo de portafolio seleccionado
      const endpoint = `https://value-investing-backend.herokuapp.com/api/portfolio/${activeTab}`;
      
      console.log(`Enviando solicitud a ${endpoint}`, formData);
      
      const res = await fetch(endpoint, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(formData)
      });
      
      if (!res.ok) {
        const errorData = await res.text();
        throw new Error(`Error ${res.status}: ${errorData || res.statusText}`);
      }
      
      const result = await res.json();
      console.log('Datos recibidos:', result);
      setPortfolioData(result);
    } catch (e) {
      console.error('Error en la solicitud:', e);
      setError(`Error al conectar con el backend: ${e.message}`);
      setPortfolioData(null);
    } finally {
      setLoading(false);
    }
  };

  // Cambiar entre tipos de portafolio
  const handleTabChange = (tab) => {
    setActiveTab(tab);
    setPortfolioData(null); // Limpiar datos anteriores al cambiar de pestaña
  };

  return (
    <div className="app-container">
      <header className="app-header">
        <h1>Value Investing Portfolio</h1>
        <p>Optimiza tu portafolio de inversiones basado en principios de value investing</p>
      </header>
      
      <main className="app-main">
        <div className="tabs">
          <button 
            className={`tab ${activeTab === 'optimize' ? 'active' : ''}`}
            onClick={() => handleTabChange('optimize')}
          >
            Optimizar Portafolio
          </button>
          <button 
            className={`tab ${activeTab === 'value' ? 'active' : ''}`}
            onClick={() => handleTabChange('value')}
          >
            Value
          </button>
          <button 
            className={`tab ${activeTab === 'growth' ? 'active' : ''}`}
            onClick={() => handleTabChange('growth')}
          >
            Growth
          </button>
          <button 
            className={`tab ${activeTab === 'disruptive' ? 'active' : ''}`}
            onClick={() => handleTabChange('disruptive')}
          >
            Disruptivas
          </button>
        </div>
        
        <div className="content-container">
          {/* Formulario */}
          <section className="form-section">
            <PortfolioForm 
              onSubmit={handleFormSubmit} 
              loading={loading} 
              portfolioType={activeTab}
            />
            {error && <div className="error-message">{error}</div>}
          </section>
          
          {/* Dashboard de resultados */}
          <section className="results-section">
            {loading ? (
              <div className="loading-indicator">
                <p>Procesando solicitud...</p>
                <div className="spinner"></div>
              </div>
            ) : portfolioData ? (
              <PortfolioDashboard
                summary={portfolioData.summary}
                strategy={portfolioData.strategy}
                positions={portfolioData.positions}
                performance={portfolioData.performance}
              />
            ) : (
              <div className="empty-state">
                <h2>Sin datos de portafolio</h2>
                <p>Completa el formulario y haz clic en "Generar Portafolio" para ver los resultados.</p>
              </div>
            )}
          </section>
        </div>
      </main>
      
      <footer className="app-footer">
        <p> 2025 Value Investing Portfolio App</p>
      </footer>
    </div>
  );
}

export default App;
