import React from 'react';
import { LineChart, Line, BarChart, Bar, PieChart, Pie, Cell, ResponsiveContainer, XAxis, YAxis, CartesianGrid, Tooltip, Legend } from 'recharts';
import { TrendingUp, TrendingDown, DollarSign, BarChart2, Briefcase, Globe } from 'lucide-react';

const PortfolioDashboard = ({ summary, strategy, positions, performance }) => {
  // Si no hay datos, mostrar mensaje
  if (!positions || positions.length === 0) {
    return <div className="p-8 text-center text-gray-500">No hay datos de portafolio disponibles.</div>;
  }

  // Cálculo de datos por sector
  const sectorData = positions.reduce((acc, position) => {
    const sectorIndex = acc.findIndex(item => item.name === position.sector);
    if (sectorIndex !== -1) {
      acc[sectorIndex].value += position.valor;
    } else {
      acc.push({ name: position.sector, value: position.valor });
    }
    return acc;
  }, []);

  // Cálculo de datos por país
  const countryData = positions.reduce((acc, position) => {
    const countryIndex = acc.findIndex(item => item.name === position.pais);
    if (countryIndex !== -1) {
      acc[countryIndex].value += position.valor;
    } else {
      acc.push({ name: position.pais, value: position.valor });
    }
    return acc;
  }, []).sort((a, b) => b.value - a.value).slice(0, 5);

  // Colores para los gráficos
  const COLORS = ['#7EB9A0', '#E9C46A', '#5C71A5', '#F4A261', '#E76F51', '#264653', '#2A9D8F'];

  // Cálculo de retorno
  const calcReturn = () => {
    if (!performance || performance.length < 2) return '0.00';
    const initial = performance[0].value;
    const current = performance[performance.length - 1].value;
    return (((current - initial) / initial) * 100).toFixed(2);
  };

  const returnValue = calcReturn();
  const isPositiveReturn = returnValue > 0;

  return (
    <div className="bg-gray-50 min-h-screen text-gray-800">
      {/* Header */}
      <header className="bg-white shadow-sm p-6 mb-6">
        <div className="max-w-7xl mx-auto flex justify-between items-center">
          <h1 className="text-3xl font-bold text-gray-900">Value Investing Portfolio</h1>
          <div className="flex items-center space-x-4">
            <button className="bg-indigo-600 text-white px-4 py-2 rounded-md text-sm font-medium">
              Actualizar Portafolio
            </button>
            <span className="text-sm text-gray-500">Última actualización: Mayo 16, 2025</span>
          </div>
        </div>
      </header>

      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Resumen del Portafolio */}
        <div className="bg-white rounded-lg shadow-md p-6 mb-6">
          <h2 className="text-xl font-semibold mb-4 text-gray-800 border-b pb-2">Resumen del Portafolio</h2>
          
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-6">
            <div className="bg-gray-50 rounded-lg p-4 flex items-center">
              <div className="bg-indigo-100 p-3 rounded-full mr-4">
                <Briefcase className="h-6 w-6 text-indigo-600" />
              </div>
              <div>
                <p className="text-sm text-gray-500">Monto Total</p>
                <p className="text-2xl font-bold">${summary.montoTotal.toLocaleString()}</p>
              </div>
            </div>
            
            <div className="bg-gray-50 rounded-lg p-4 flex items-center">
              <div className="bg-green-100 p-3 rounded-full mr-4">
                <DollarSign className="h-6 w-6 text-green-600" />
              </div>
              <div>
                <p className="text-sm text-gray-500">Invertido</p>
                <p className="text-2xl font-bold">${summary.invertido.toLocaleString()}</p>
              </div>
            </div>
            
            <div className="bg-gray-50 rounded-lg p-4 flex items-center">
              <div className={`${isPositiveReturn ? 'bg-green-100' : 'bg-red-100'} p-3 rounded-full mr-4`}>
                {isPositiveReturn ? 
                  <TrendingUp className="h-6 w-6 text-green-600" /> : 
                  <TrendingDown className="h-6 w-6 text-red-600" />
                }
              </div>
              <div>
                <p className="text-sm text-gray-500">Retorno</p>
                <p className={`text-2xl font-bold ${isPositiveReturn ? 'text-green-600' : 'text-red-600'}`}>
                  {isPositiveReturn ? '+' : ''}{returnValue}%
                </p>
              </div>
            </div>
          </div>
          
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div>
              <h3 className="text-lg font-medium mb-4">Rendimiento</h3>
              <div className="h-64">
                <ResponsiveContainer width="100%" height="100%">
                  <LineChart data={performance} margin={{ top: 5, right: 20, bottom: 5, left: 0 }}>
                    <CartesianGrid strokeDasharray="3 3" stroke="#f0f0f0" />
                    <XAxis dataKey="month" stroke="#718096" />
                    <YAxis stroke="#718096" />
                    <Tooltip />
                    <Line type="monotone" dataKey="value" stroke="#5C71A5" strokeWidth={2} dot={{ stroke: '#5C71A5', strokeWidth: 2, r: 4 }} activeDot={{ r: 6 }} />
                  </LineChart>
                </ResponsiveContainer>
              </div>
            </div>
            
            <div>
              <h3 className="text-lg font-medium mb-4">Distribución por Estrategia</h3>
              <div className="h-64 flex justify-center items-center">
                <ResponsiveContainer width="100%" height="100%">
                  <PieChart>
                    <Pie
                      data={strategy}
                      cx="50%"
                      cy="50%"
                      innerRadius={60}
                      outerRadius={90}
                      paddingAngle={2}
                      dataKey="value"
                      label={({name, percent}) => `${name}: ${(percent * 100).toFixed(0)}%`}
                    >
                      {strategy.map((entry, index) => (
                        <Cell key={`cell-${index}`} fill={entry.color} />
                      ))}
                    </Pie>
                    <Tooltip />
                  </PieChart>
                </ResponsiveContainer>
              </div>
            </div>
          </div>
        </div>
        
        {/* Tabla de Posiciones */}
        <div className="bg-white rounded-lg shadow-md p-6 mb-6">
          <div className="flex justify-between items-center mb-4">
            <h2 className="text-xl font-semibold text-gray-800">Posiciones</h2>
            <div className="flex space-x-2">
              <button className="bg-white border border-gray-300 text-gray-700 px-3 py-1 rounded-md text-sm">
                Exportar
              </button>
              <button className="bg-white border border-gray-300 text-gray-700 px-3 py-1 rounded-md text-sm">
                Filtrar
              </button>
            </div>
          </div>
          
          <div className="overflow-x-auto">
            <table className="min-w-full divide-y divide-gray-200">
              <thead className="bg-gray-50">
                <tr>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Ticker</th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Estrategia</th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Sector</th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">País</th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Acciones</th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Precio</th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Valor</th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200">
                {positions.map((position, index) => (
                  <tr key={index} className={index % 2 === 0 ? 'bg-white' : 'bg-gray-50'}>
                    <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">{position.ticker}</td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      <span className={`px-2 inline-flex text-xs leading-5 font-semibold rounded-full 
                        ${position.estrategia === 'value' ? 'bg-green-100 text-green-800' : 
                          position.estrategia === 'growth' ? 'bg-yellow-100 text-yellow-800' : 
                          'bg-blue-100 text-blue-800'}`}>
                        {position.estrategia}
                      </span>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">{position.sector}</td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">{position.pais}</td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">{position.acciones}</td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">${position.precio}</td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900 font-medium">${position.valor}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
        
        {/* Análisis Adicionales */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-6">
          {/* Distribución por Sector */}
          <div className="bg-white rounded-lg shadow-md p-6">
            <h2 className="text-lg font-semibold text-gray-800 mb-4">Distribución por Sector</h2>
            <div className="h-64">
              <ResponsiveContainer width="100%" height="100%">
                <PieChart>
                  <Pie
                    data={sectorData}
                    cx="50%"
                    cy="50%"
                    outerRadius={80}
                    fill="#8884d8"
                    dataKey="value"
                    label={({name, percent}) => name.length > 10 ? `${name.substring(0, 10)}...: ${(percent * 100).toFixed(0)}%` : `${name}: ${(percent * 100).toFixed(0)}%`}
                  >
                    {sectorData.map((entry, index) => (
                      <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                    ))}
                  </Pie>
                  <Tooltip formatter={(value) => `$${value}`} />
                </PieChart>
              </ResponsiveContainer>
            </div>
          </div>
          
          {/* Distribución Geográfica */}
          <div className="bg-white rounded-lg shadow-md p-6">
            <h2 className="text-lg font-semibold text-gray-800 mb-4">Distribución Geográfica</h2>
            <div className="h-64">
              <ResponsiveContainer width="100%" height="100%">
                <BarChart
                  data={countryData}
                  layout="vertical"
                  margin={{ top: 5, right: 30, left: 80, bottom: 5 }}
                >
                  <CartesianGrid strokeDasharray="3 3" stroke="#f0f0f0" />
                  <XAxis type="number" />
                  <YAxis type="category" dataKey="name" width={100} />
                  <Tooltip formatter={(value) => `$${value}`} />
                  <Bar dataKey="value" fill="#5C71A5" />
                </BarChart>
              </ResponsiveContainer>
            </div>
          </div>
        </div>
        
        {/* Métricas Clave */}
        <div className="bg-white rounded-lg shadow-md p-6 mb-6">
          <h2 className="text-lg font-semibold text-gray-800 mb-4">Métricas Clave</h2>
          
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
            <div className="bg-gray-50 rounded-lg p-4">
              <p className="text-sm text-gray-500 mb-1">P/E Promedio</p>
              <p className="text-xl font-bold">22.3</p>
            </div>
            
            <div className="bg-gray-50 rounded-lg p-4">
              <p className="text-sm text-gray-500 mb-1">Div. Promedio</p>
              <p className="text-xl font-bold">2.4%</p>
            </div>
            
            <div className="bg-gray-50 rounded-lg p-4">
              <p className="text-sm text-gray-500 mb-1">Volatilidad</p>
              <p className="text-xl font-bold">12.5%</p>
            </div>
            
            <div className="bg-gray-50 rounded-lg p-4">
              <p className="text-sm text-gray-500 mb-1">Sharpe Ratio</p>
              <p className="text-xl font-bold">1.2</p>
            </div>
          </div>
        </div>
        
        {/* Análisis Detallado */}
        <div className="bg-white rounded-lg shadow-md p-6 mb-6">
          <h2 className="text-lg font-semibold text-gray-800 mb-4">Análisis del Portafolio</h2>
          
          <div className="prose max-w-none">
            <p className="text-gray-700">
              El portafolio analizado presenta una capitalización total de 2,000 unidades monetarias, con una distribución diversificada entre diferentes clases de activos, sectores y regiones geográficas.
            </p>
            <p className="text-gray-700">
              La mayor asignación corresponde a la estrategia de Value (50%), seguida de Growth y Bonds con un 25% cada una. Los sectores más representados incluyen Seguros, Tecnología y Renta Fija.
            </p>
            <p className="text-gray-700">
              Los indicadores fundamentales muestran un P/E promedio de 22.3 y un rendimiento por dividendo promedio de 2.4%, lo que sugiere una composición balanceada entre valor y crecimiento.
            </p>
          </div>
        </div>
      </main>
      
      <footer className="bg-white border-t mt-6 py-4">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <p className="text-sm text-gray-500 text-center">© 2025 Value Investing Portfolio App. Todos los derechos reservados.</p>
        </div>
      </footer>
    </div>
  );
};

export default PortfolioDashboard;
